import { mapGetters } from 'vuex';
export default {
	components: {},
	computed: {
		...mapGetters('footprint', {
			goods: 'goods'
		}),
		...mapGetters('footprint',{
			invalidGoods:'invalidGoods'
		})
	},
	data() {
		return{}
	},
	onLoad: function(options) {
		this.getData();
	},
	methods: {
		async getData() {
			uni.showLoading({
				title: '数据加载中...'
			});
			await this.$store.dispatch('footprint/GetGoods');
			uni.hideLoading();
		},
		goodClick(g){
			let params = {
				id: g.id,
				numm_id: g.goods_id
			};
			uni.showLoading({
				title: '数据加载中...'
			});
			this.$store.dispatch('good/GetGoodDetail', params).then(()=>{
				uni.hideLoading();
				uni.navigateTo({
					url: '/pages/good/detail?id='+g.id+"&nummId="+g.goods_id
				})
				
			}).catch(()=>{
				//TODO
				uni.hideLoading();
			});
		},
		clearInvalidGood(){
			
		}
	},
	onNavigationBarButtonTap() {
		uni.showModal({
			title: '提示',
			content: '确定删除浏览记录吗?删除无法恢复！',
			success: res => {
				if (res.confirm) {
					this.$store.dispatch('footprint/CleanGoods');
				}
			}
		});
	}
};
